import React, { useMemo, useState } from "react";
import "./QuarterlyReport.css";
import { useTaskData } from "../../hooks/useTaskData";
import { useSearch } from "../../hooks/useSearch";
import { CSVUploader } from "../CSVUploader/CSVUploader";
import { DataTable } from "../DataTable/DataTable";
import { Input } from "../Input/Input";
import { Chart } from "../Charts/Chart";
import { Stats } from "../Stats/Stats";

export const QuarterlyReport: React.FC = () => {
  const [selectedTeam, setSelectedTeam] = useState<string | null>(null);

  const {
    data,
    taskTypeStats,
    estimateStats,
    taskAssigneeStats,
    taskReporterStats,
    taskPriorityStats,
    labelStats,
    teamStats,
    handleOnDrop,
    clearData,
  } = useTaskData(selectedTeam);

  const { globalFilter, handleSearchChange, handleClearSearch } = useSearch();

  const sortedTeamStats = useMemo(() => {
    return [...teamStats].sort((a, b) => {
      if (a.team === " ") return 1;
      if (b.team === " ") return -1;

      if (a.count === 0 && b.count > 0) return 1;
      if (b.count === 0 && a.count > 0) return -1;

      return b.count - a.count;
    });
  }, [teamStats]);

  // const handleTeamSelect = (team: string) => {
  //   setSelectedTeam(prev => prev === team ? null : team);
  // };

  return (
    <div>
      <div className="title-section">
        <h1>    </h1>
      </div>

      <CSVUploader onDrop={handleOnDrop} onClear={clearData} />

      {data.length > 0 && (
        <div>
          <div className="team-stats-section">
            {sortedTeamStats.map((stat) => (
              <Stats
                key={stat.team}
                count={stat.count}
                estimate={stat.estimate}
                teamName={stat.team}
                isSelected={selectedTeam === stat.team}
                onClick={() =>
                  setSelectedTeam((prev) =>
                    prev === stat.team ? null : stat.team
                  )
                }
                disabled={stat.count === 0}
              />
            ))}
          </div>

          <div className="charts-section">
            <Chart
              data={taskTypeStats}
              name="   "
              dataKey="count"
            />
            <Chart
              data={taskPriorityStats}
              name="   "
              dataKey="count"
            />
            <Chart
              data={estimateStats}
              name="   "
              dataKey="estimate"
            />
            <Chart
              data={taskAssigneeStats}
              name="   "
              dataKey="count"
            />
            <Chart
              data={taskReporterStats}
              name="   "
              dataKey="count"
            />
            <Chart
              data={labelStats}
              name="   "
              dataKey="count"
            />
            {/* <Chart data={} name='' dataKey='' /> */}
          </div>

          <div className="table-section">
            <h2>{data.length} </h2>

            <Input
              value={globalFilter}
              onChange={handleSearchChange}
              onClear={handleClearSearch}
              placeholder=""
            />

            <DataTable data={data} globalFilter={globalFilter} />
          </div>
        </div>
      )}
    </div>
  );
};

export default QuarterlyReport;
